<template>
  <div class="ShipmentTimeOption">
    <v-dialog v-model="dialog" width="50vw">
      <template v-slot:activator="{ on }">
        <v-btn class="primary" dark v-on="on" @click="enterProcess">
          出貨單資料列表
        </v-btn>
      </template>
      
      <v-form ref="form" class="mt-3">
        <v-card :color="$store.state.colorSet.bgColor1">
          <v-card-title :class="titleClass" primary-title>{{title}}</v-card-title>

          <v-card-text>
            <v-alert color="primary" outlined class="mt-2">
              <v-text-field
                label="出貨單起始時間"
                type="date"
                v-model="list.startDay"
                :rules="emptyRules"
              ></v-text-field>
              <v-divider class="my-3"></v-divider>
              <v-text-field label="出貨單結束時間" type="date" v-model="list.endDay" :rules="emptyRules"></v-text-field>
            </v-alert>
          </v-card-text>

          <v-divider></v-divider>

          <v-card-actions>
            <v-btn color="primary" outlined @click="setThisMonth">本月</v-btn>
            <v-spacer></v-spacer>
            <v-btn class="primary" @click="print">顯示指定資料</v-btn>
          </v-card-actions>
          <!-- <pre>{{list}}</pre> -->
        </v-card>
      </v-form>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {startDay:"",endDay:""},
      emptyRules: [v => !!v || "不可空白"],
      title: "", //抬頭
      titleClass: "", //抬頭顯示樣式
    };
  },

  methods: {
    enterProcess() {
      //執行修改流程(本案只拿來顯示畫面,透過父項目的控制)
      this.title = "出貨單資料列表設定";
      this.titleClass = "font-weight-black primary lighten-2";
    },
    setThisMonth(){
      this.list.startDay = dayjs().startOf('month').format("YYYY-MM-DD");
      this.list.endDay = dayjs().endOf('month').format("YYYY-MM-DD");
    },
    //確認顯示
    print(){
      if (this.$refs.form.validate()) {
        let routeData = this.$router.resolve({path: "/print/shipmentlistprint",query:{data:JSON.stringify(this.list)}});
        window.open(routeData.href, "_blank");
      }
    }
  }
};
</script>

