<template>
  <div class="shipmentAdd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card :color="$store.state.colorSet.bgColor1">
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}
        <span v-if="editTempModel" class="red white--text">[出貨單列表->單項修改模式]</span>
        </v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" sm="2">
                <v-text-field
                  label="日期"
                  type="date"
                  prepend-icon="mdi-calendar-today"
                  v-model="list.date"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="1">
                <v-text-field
                  label="單據號碼"
                  prepend-icon="mdi-numeric"
                  v-model="list.code"
                  readonly
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="4" class="d-flex align-center">
                <v-autocomplete
                  label="客戶統編 / 名稱"
                  prepend-icon="mdi-office-building"
                  v-model="list.customer"
                  :rules="emptyRules"
                  :items="customerArr"
                  v-if="dialog"
                  autofocus
                  return-object
                ></v-autocomplete>
                <CustomerDialog v-if="auth.customer_key" />
              </v-col>
              <v-col cols="12" sm="3">
                <v-text-field
                  label="指定送貨地址"
                  prepend-icon="mdi-home-circle"
                  v-model="list.otheraddress"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="2">
                <v-checkbox
                  label="現金價不折扣"
                  v-model="list.noDiscount"
                ></v-checkbox>
              </v-col>
              <v-col cols="12" sm="6" class="d-flex align-center">
                <v-checkbox
                  class="mr-1"
                  v-model="linkCustomer"
                  label="連結客戶"
                ></v-checkbox>
                <v-autocomplete
                  label="品名"
                  prepend-icon="mdi-codepen"
                  v-model="list.rawmaterial"
                  :rules="emptyRules"
                  :items="rawmaterialArr"
                  return-object
                  ref="rawmaterial"
                ></v-autocomplete>
                <RawmaterialDialog v-if="auth.rawmaterial_key" />
                
              </v-col>
              <v-col cols="12" sm="4" class="d-flex">
                <v-checkbox label="無規格" class="text-no-wrap mr-3" v-model="list.noSize"></v-checkbox>
                <v-text-field
                  label="寬度"
                  type="number"
                  prepend-icon="mdi-arrow-split-horizontal"
                  v-model="list.width"
                  v-if="!list.noSize"
                  :rules="emptyRules"
                ></v-text-field>
                <v-text-field
                  label="長度"
                  type="number"
                  prepend-icon="mdi-arrow-split-vertical"
                  v-model="list.long"
                  v-if="!list.noSize"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="2">
                <v-text-field
                  label="數量"
                  type="number"
                  prepend-icon="mdi-counter"
                  v-model="list.quantity"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="4" class="d-flex align-center">
                <v-autocomplete
                  label="沖鎖"
                  prepend-icon="mdi-lock"
                  v-model="list.punchlock"
                  :items="punchlockArr"
                  return-object
                  clearable
                >
                  <template v-slot:selection="data">
                    <div class="mr-2">
                      {{ data.item.name }} / {{ data.item.price }}
                    </div>
                    <v-img
                      max-width="40px"
                      :src="
                        $store.state.base_url +
                        '/upload/punchlock/' +
                        data.item.picname
                      "
                    ></v-img>
                  </template>
                  <template v-slot:item="data">
                    <template>
                      <v-list-item-content>
                        <v-list-item-title class="d-flex justify-space-between">
                          <div class="mr-2">
                            {{ data.item.name }} / {{ data.item.price }}
                          </div>
                          <v-img
                            max-width="80px"
                            :src="
                              $store.state.base_url +
                              '/upload/punchlock/' +
                              data.item.picname
                            "
                          ></v-img>
                        </v-list-item-title>
                      </v-list-item-content>
                    </template>
                  </template>
                </v-autocomplete>
                <PunchlockDialog v-if="auth.punchlock_key" />
              </v-col>
              <v-col cols="12" sm="4" class="d-flex align-center">
                <v-autocomplete
                  label="折工"
                  prepend-icon="mdi-shape-polygon-plus"
                  v-model="list.fold"
                  :items="foldArr"
                  return-object
                  clearable
                >
                  <template v-slot:selection="data">
                    <div class="mr-2">
                      {{ data.item.name }} / {{ data.item.price }}
                    </div>
                    <v-img
                      max-width="40px"
                      :src="
                        $store.state.base_url +
                        '/upload/fold/' +
                        data.item.picname
                      "
                    ></v-img>
                  </template>
                  <template v-slot:item="data">
                    <template>
                      <v-list-item-content>
                        <v-list-item-title class="d-flex justify-space-between">
                          <div class="mr-2">
                            {{ data.item.name }} / {{ data.item.price }}
                          </div>
                          <v-img
                            max-width="80px"
                            :src="
                              $store.state.base_url +
                              '/upload/fold/' +
                              data.item.picname
                            "
                          ></v-img>
                        </v-list-item-title>
                      </v-list-item-content>
                    </template>
                  </template>
                </v-autocomplete>
                <FoldDialog v-if="auth.fold_key" />
              </v-col>
              <v-col cols="12" sm="4" class="d-flex align-center">
                <v-autocomplete
                  label="加工"
                  prepend-icon="mdi-plus-network"
                  v-model="list.machining"
                  :items="machiningArr"
                  return-object
                  clearable
                >
                  <template v-slot:selection="data">
                    <div class="mr-2">
                      {{ data.item.name }} / {{ data.item.price }}
                    </div>
                    <v-img
                      max-width="40px"
                      :src="
                        $store.state.base_url +
                        '/upload/machining/' +
                        data.item.picname
                      "
                    ></v-img>
                  </template>
                  <template v-slot:item="data">
                    <template>
                      <v-list-item-content>
                        <v-list-item-title class="d-flex justify-space-between">
                          <div class="mr-2">
                            {{ data.item.name }} / {{ data.item.price }}
                          </div>
                          <v-img
                            max-width="80px"
                            :src="
                              $store.state.base_url +
                              '/upload/machining/' +
                              data.item.picname
                            "
                          ></v-img>
                        </v-list-item-title>
                      </v-list-item-content>
                    </template>
                  </template>
                </v-autocomplete>
                <MachiningDialog v-if="auth.machining_key" />
              </v-col>
              
              <v-col cols="12" sm="6">
                <v-text-field
                  label="備註"
                  prepend-icon="mdi-note-text"
                  v-model="list.note"
                ></v-text-field>
              </v-col>
              
              <v-col cols="12" sm="6">
                <v-text-field
                  label="內部備註內容"
                  prepend-icon="mdi-microsoft-onenote"
                  v-model="list.insideNote"
                ></v-text-field>
              </v-col>
              
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addtoTemp" v-if="!editTempModel">
            <v-icon>mdi-plus</v-icon>
            新增到出貨單</v-btn
          >
          <v-btn class="success" @click="edittoTemp" v-else>
            <v-icon>mdi-plus</v-icon>
            確認修改出貨單內容</v-btn
          >
        </v-card-actions>

        <!-- 出貨單列表 -->
        <v-card v-if="tempItems.length">
          <v-card-title
            class="blue lighten-4 font-weight-black justify-space-between"
          >
            <h2>出貨單列表</h2> 

            <v-btn
              large
              dark
              @click="addOK"
              v-if="processType == 'add' && tempItems.length"
              class="text-h5"
              >
              <v-icon class="mr-2">mdi-text-box-check-outline</v-icon>
              確認新增出貨單</v-btn
            >
            <v-btn
              large
              class="success text-h5"
              @click="editOK"
              v-if="processType == 'edit'"
            >
              <v-icon class="mr-2">mdi-text-box-check-outline</v-icon>確認修改出貨單</v-btn
            >
            
          </v-card-title>
          <v-card-text>
            <div class="d-flex justify-space-between my-2">
              <div>
                <v-chip color="yellow" class="text-h6 font-weight-black"
                  >品項: {{ tempItems.length }}</v-chip
                >
                <v-chip color="yellow" class="text-h6 font-weight-black"
                  >數量: {{ quantityTotal }}</v-chip
                >
              </div>
              <div>
                <v-chip color="primary" class="text-h6 font-weight-black">買賣金額: {{ priceTotal }}</v-chip>
                <v-chip color="primary" class="text-h6 font-weight-black">沖鎖金額: {{ optionTotal('punchlock','extraPunchlockPrice') }}</v-chip>
                <v-chip color="primary" class="text-h6 font-weight-black">折工金額: {{ optionTotal('fold','extraFoldPrice') }}</v-chip>
                <v-chip color="primary" class="text-h6 font-weight-black">加工金額: {{ optionTotal('machining','extraMachiningPrice') }}</v-chip
                >
              </div>

              <v-chip color="error" class="text-h6 font-weight-black"
                >出貨單總金額: 
                {{ priceTotal + optionTotal('punchlock','extraPunchlockPrice') + optionTotal('fold','extraFoldPrice') + optionTotal('machining','extraMachiningPrice') }}</v-chip
              >
            </div>

            <v-simple-table class="text-no-wrap" fixed-header>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">品名</th>
                    <th class="text-left">規格</th>
                    <th class="text-left">數量</th>
                    <th class="text-left">公斤</th>
                    <th class="text-left">單價</th>
                    <th class="text-left">單價調整</th>

                    <th class="text-left">買賣金額</th>
                    
                    <th class="text-left">沖鎖金額</th>
                    <th class="text-left">沖鎖調整</th>

                    <th class="text-left">折工金額</th>
                    <th class="text-left">折工調整</th>
                    
                    <th class="text-left">加工金額</th>
                    <th class="text-left">加工調整</th>

                    <th class="text-left">備註</th>
                    <th class="text-left">內部備註內容</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in tempItems" :key="index">
                    <td>
                      <v-icon class="success mr-2" dark @click.stop="editfromTemp(index,item)"
                        >mdi-pencil</v-icon
                      >
                      <v-icon class="error mr-2" dark @click.stop="delfromTemp(index)"
                        >mdi-close</v-icon
                      >
                      {{ item.rawmaterial.thickness + ' / ' + item.rawmaterial.name }}
                    </td>
                    <td>
                      <div
                        class="d-flex justify-space-between align-center"
                        style="width: 250px"
                      >
                        <span v-if="!item.noSize">{{ item.width }} * {{ item.long }}</span>
                        
                        <v-img
                          max-width="60px"
                          :src="$store.state.base_url + '/upload/punchlock/' + item.punchlock.picname"
                          v-if="item.punchlock"
                        ></v-img>
                        <v-img
                          max-width="60px"
                          :src="$store.state.base_url + '/upload/fold/' + item.fold.picname"
                          v-if="item.fold"
                        ></v-img>
                        <v-img
                          max-width="60px"
                          :src="$store.state.base_url + '/upload/machining/' + item.machining.picname"
                          v-if="item.machining"
                        ></v-img>
                      </div>
                    </td>
                    <td>
                      <v-text-field
                        type="number"
                        style="width: 80px"
                        dense
                        outlined
                        hide-details
                        v-model="item.quantity"
                      ></v-text-field>
                    </td>
                    <td>{{ filterweight(item) }}</td>
                    
                    <td>
                      {{
                        parseFloat(item.rawmaterial.price) + parseFloat(item.extraRawmaterialPrice)
                      }}
                    </td>
                    <td>
                      <!-- 單價調整 -->
                      <v-text-field
                        type="number"
                        style="width: 100px"
                        dense
                        outlined
                        hide-details
                        v-model="item.extraRawmaterialPrice"
                      ></v-text-field>
                    </td>

                    <td>{{ filterFinalPrice(item) }}</td>
                    <td>
                      <!-- 沖鎖金額 -->
                      <div v-if="item.punchlock">
                        {{ (parseFloat(item.punchlock.price) + parseFloat(item.extraPunchlockPrice) * item.quantity) }}        
                      </div>  
                    </td>
                    <td>
                      <!-- 沖鎖調整 -->
                      <v-text-field
                        type="number"
                        style="width: 100px"
                        dense
                        outlined
                        hide-details
                        v-model="item.extraPunchlockPrice"
                        v-if="item.punchlock"
                      ></v-text-field>
                    </td>
                    <td>
                      <!-- 折工金額 -->
                      <div v-if="item.fold">
                        {{ (parseFloat(item.fold.price) + parseFloat(item.extraFoldPrice)) * item.quantity }}        
                      </div>  
                    </td>
                    <td>
                      <!-- 折工調整 -->
                      <v-text-field
                        type="number"
                        style="width: 100px"
                        dense
                        outlined
                        hide-details
                        v-model="item.extraFoldPrice"
                        v-if="item.fold"
                      ></v-text-field>
                    </td>
                    <td>
                      <!-- 加工金額 -->
                      <div v-if="item.machining">
                        {{ (parseFloat(item.machining.price) + parseFloat(item.extraMachiningPrice)) * item.quantity }}        
                      </div>  
                    </td>
                    <td>
                      <!-- 加工調整 -->
                      <v-text-field
                        type="number"
                        style="width: 100px"
                        dense
                        outlined
                        hide-details
                        v-model="item.extraMachiningPrice"
                        v-if="item.machining"
                      ></v-text-field>
                    </td>
                    
                    <td style="min-width:300px">
                      <v-text-field
                        dense
                        outlined
                        hide-details
                        v-model="item.note"
                        full-width
                      ></v-text-field>
                    </td>
                    <td style="min-width:300px">
                      <v-text-field
                        dense
                        outlined
                        hide-details
                        v-model="item.insideNote"
                        full-width
                      ></v-text-field>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-card-text>
        </v-card>
        <!-- <pre>list:{{ list }}</pre> -->
        <!-- <pre>rawmaterialArr:{{ rawmaterialArr }}</pre> -->
        <!-- <pre>tempItems:{{ tempItems }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";
import CustomerDialog from "@/views/main/CustomerDialog";
import RawmaterialDialog from "@/views/main/RawmaterialDialog";
import FoldDialog from "@/views/main/FoldDialog";
import PunchlockDialog from "@/views/main/PunchlockDialog";
import MachiningDialog from "@/views/main/MachiningDialog";

import * as math from 'mathjs';  //引入mathjs,處理浮點相乘產生的問題

export default {
  props: ["shipmentItems", "auth"],
  components: { CustomerDialog, RawmaterialDialog, FoldDialog ,PunchlockDialog, MachiningDialog },
  data() {
    return {
      dialog: false,
      list: {
        quantity: 1,
      }, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      tempItems: [], //暫存出貨單品項
      editTempModel:false, //暫存出貨單修改模式
      editTempModelIndex:null, //暫存出貨單修改模式中的index值
      linkCustomer:true, //連結廠商資料
    };
  },

  computed: {
    //數量總額
    quantityTotal() {
      return this.tempItems.reduce((total, item) => {
        return (total += parseFloat(item.quantity));
      }, 0);
    },
    //單價總金額
    priceTotal() {
      return this.tempItems.reduce((total, item) => {
        return (total += this.filterFinalPrice(item));
      }, 0);
    },
    //額外加工金額
    extraProcessPriceTotal() {
      return this.tempItems.reduce((total, item) => {
        if (item.extraProcessPrice) {
          return (total += parseFloat(item.extraProcessPrice) * item.quantity);
        } else {
          return total;
        }
      }, 0);
    },
    customerArr() {
      return this.$store.state.customerItems.map((i) => {
        return {
          text: `${i.code} / ${i.name}`,
          snkey: i.snkey,
          code: i.code,
          name: i.name,
        };
      });
    },
    //回傳品名 2021.4.1 設定連結廠商group 主要是為了選出指定廠商適用的品名價格
    rawmaterialArr() {
      if (this.linkCustomer){
        let matchRawItems = this.$store.state.rawmaterialItems.filter(rawItem=>{
            if (rawItem.customers){
              let match = rawItem.customers.find(customer=>{
                return (this.list.customer && customer.snkey == this.list.customer.snkey)
              });
              if (match){
                return rawItem;
              }
            }
        });
        return matchRawItems.map((i) => {
          let newItem = {
            text: `${i.thickness} / ${i.name} / ${i.price}`,
            snkey: i.snkey,
            name: i.name,
            thickness: i.thickness,
            price: i.price,
            picname: i.picname,
          };
          if (!i.proportion){ //標未設定前,鐵,鋼比重都是7.93
            newItem.proportion = 7.93
          }else{
            newItem.proportion = i.proportion
          }  
          return newItem;
        });
      }else{
        return this.$store.state.rawmaterialItems.map((i) => {
          let newItem = {
            text: `${i.thickness} / ${i.name} / ${i.price}`,
            snkey: i.snkey,
            name: i.name,
            thickness: i.thickness,
            price: i.price,
            picname: i.picname,
          };
          if (!i.proportion){ //標未設定前,鐵,鋼比重都是7.93
            newItem.proportion = 7.93
          }else{
            newItem.proportion = i.proportion
          }  
          return newItem;
        });

      }
    },
    punchlockArr() {
      return this.$store.state.punchlockItems.map((i) => {
        return {
          text: `${i.name} / ${i.price}`,
          snkey: i.snkey,
          picname: i.picname,
          price: i.price,
          name: i.name,
        };
      });
    },
    
    foldArr() {
      return this.$store.state.foldItems.map((i) => {
        return {
          text: `${i.name} / ${i.price}`,
          snkey: i.snkey,
          picname: i.picname,
          price: i.price,
          name: i.name,
        };
      });
    },
    machiningArr() {
      return this.$store.state.machiningItems.map((i) => {
        return {
          text: `${i.name} / ${i.price}`,
          snkey: i.snkey,
          picname: i.picname,
          price: i.price,
          name: i.name,
        };
      });
    },
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
    this.$store.dispatch("getItems", "customer"); //取得客戶資料 放在store裡
    this.$store.dispatch("getItems", "rawmaterial"); //取得品名資料 放在store裡
    this.$store.dispatch("getItems", "fold"); //取得折工資料 放在store裡
    this.$store.dispatch("getItems", "punchlock"); //取得沖鎖資料 放在store裡
    this.$store.dispatch("getItems", "machining"); //取得加工資料 放在store裡
  },

  methods: {
    //各種工作總金額
    optionTotal(type,extraType) {
      return this.tempItems.reduce((total, item) => {
        if (item[type]){
          return (total += (parseFloat(item[type].price) + parseFloat(item[extraType])) * item.quantity);
        }else{
          return total;
        }
      }, 0);
    },
    //建立單據號碼
    createCode() {
      return this.shipmentItems.filter((i) => {
        return i.date == dayjs().format("YYYY-MM-DD");
      });
    },
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增出貨單";
      this.titleStyle = "font-weight-black error lighten-2";
      
      //表單內容初期化
      this.list = {
        date :dayjs().format("YYYY-MM-DD")
      }
      this.tempItems = [];

      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });

      this.list.code = this.createCode().length + 1; //建立單據號碼
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改出貨單資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結

      this.list = { ...item }; //複制一份實際修改資料內容
      this.tempItems = [...this.list.tempItems];
      delete this.list.tempItems;
    },
    //新增到出貨單暫存區
    addtoTemp() {
      if (this.$refs.form.validate()) {
        let newItem = { ...this.list };
        newItem.weight = this.filterweight(newItem);
        newItem.extraRawmaterialPrice= 0
        newItem.extraFoldPrice= 0
        newItem.extraPunchlockPrice= 0
        newItem.extraMachiningPrice= 0
        
        this.tempItems.push(newItem);
        this.$swal({ icon: "success", text: "已新增到出貨單" });
        this.$refs.rawmaterial.focus();
      }
    },
    //修改暫存區出貨單 -> 開始進入修改模式
    editfromTemp(index,item){
      this.list = {...item}
      this.editTempModel = true;
      this.editTempModelIndex = index;
    },
    //確認修改暫存區出貨單
    edittoTemp(){
      this.list.weight = this.filterweight(this.list);
      this.tempItems.splice(this.editTempModelIndex,1,{...this.list});
      this.editTempModel = false;
      this.editTempModelIndex = null;
    },
    delfromTemp(index) {
      this.tempItems.splice(index, 1);
    },
    //額外加工顯示
    filterExtraProcess(item) {
      if (item.extraProcess) {
        return (
          item.extraProcess + " / " + item.extraProcessPrice * item.quantity
        );
      } else {
        return "";
      }
    },
    //最終單價計算
    filterFinalPrice(item) {
      if (item.noSize){
        var val = item.quantity * (parseFloat(item.rawmaterial.price) + parseFloat(item.extraRawmaterialPrice));
      }else{
        var val = item.weight * (parseFloat(item.rawmaterial.price) + parseFloat(item.extraRawmaterialPrice));
      }
      return Math.round(Number(math.format(val, 14)));  
      /*
      2021.3.19 改回傳四捨五入,取整數
      2021.7.3 Number(math.format(val, 14)) 裡面是透過mathjs去處理相乘時會有小數點的情況                                  
      */
      // return this.roundUp(val, 0);
    },
    //重量計算
    filterweight(item) {
      if (item.noSize){return ''}
      if (!item.rawmaterial.proportion){
        item.rawmaterial.proportion = 7.93
      }  //支援舊資料不存在proportion時,要給預設值7.93
      let val =
        item.rawmaterial.thickness *
        (item.width / 1000) *
        (item.long / 1000) *
        item.rawmaterial.proportion *
        item.quantity;

      return (Math.round(val*10)/10);  //2021.3.19 改回傳四捨五入,取小數點一位
      //無條件進位函數
      // return this.roundUp(val, 1);
    },
    //無條件進位
    // roundUp(num, decimal) {
    //   return (
    //     Math.ceil((num + Number.EPSILON) * Math.pow(10, decimal)) /
    //     Math.pow(10, decimal)
    //   );
    // },

    //確認新增出貨單
    addOK() {
      this.yn("確認新增出貨單").then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          this.list.personnel_snkey = this.$store.state.pData.snkey;
          this.list.create_man =
            this.$store.state.pData.name +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")"; //執行動作的人,時間,內容
          this.list.tempItems = this.tempItems;

          let postData = {
            personnel_snkey: this.list.personnel_snkey,
            datalist: JSON.stringify(this.list),
          };

          this.dialog = false;
          this.myAxios(
            "general/add/" + this.$store.state.databaseName + "/shipment",
            postData
          )
            .then((rs) => {
              if (rs.state == 1) {
                this.$swal({ icon: "success", text: "已新增" });
                this.$emit("getAllData");
              }
            })
            .catch((err) => {
              this.$swal({ icon: "err", text: "資料存取錯誤!!內容:" + err });
            });
        }
      });
    },
    //確認修改出貨單
    editOK() {
      this.yn("確認修改出貨單").then((result) => {
        if (result.isConfirmed) {
          if (this.list.edit_man == undefined) {
            this.list.edit_man = "";
          }
          this.list.edit_man =
            this.$store.state.pData.name +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")" +
            this.list.edit_man; //執行動作的人,時間,內容
          this.list.tempItems = this.tempItems;
          this.list.snkey = this.oldList.snkey; //因為會直接取用tempItems裡的值去取代list的內容,新增的資料有時會沒有之前的snkey值

          let postData = {
            snkey: this.list.snkey,
            datalist: JSON.stringify(this.list),
          };

          this.dialog = false;
          this.myAxios(
            "general/edit/" + this.$store.state.databaseName + "/shipment",
            postData
          ).then((rs) => {
            if (rs.state == 1) {
              this.$swal({
                icon: "success",
                text: "已修改",
              });
              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
            }
          });
        }
      });
    },
  },
};
</script>

